a {
  text-decoration: none;
  color: inherit;
}

.header {
  min-height: 10vh;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
}

.header h2 {
  margin: 0;
}

.mainContainer {
  display: flex;
  justify-content: space-around;
  height: 80vh;
}

.footer {
  min-height: 10vh;
  padding: 10px;
  box-sizing: border-box;
  vertical-align: bottom;
  display: flex;
  justify-content: center;
  align-items: center;
}

.canvasContainer {
  width: 60vw;
  border-radius: 10%;
  overflow: hidden;
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.5);
}

.button {
  width: 100px;
  height: 40px;
  text-align: center;
}

.labelBtn {
  display: block;
  cursor: pointer;
  line-height: 1.8em;
  color: white;
  background: #20b8be;
  border-radius: 5px;
  font-weight: 600;
  white-space: nowrap;
  line-height: 2rem;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.button input[type='radio'] {
  opacity: 0;
  visibility: hidden;
}

.button input[type='radio']:checked + .labelBtn {
  background: #2254fa;
}

.labelBtn:hover {
  background-color: #17a2b8;
  border-color: #17a2b8;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(200, 223, 253);
}

